@use 'variables';

.hero {
    padding-top: 64px;
    padding-bottom: 100px;
    background-image: url(../images/fundo-festa.jpg); 
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    &__title {
        font-family: 'Pacifico', cursive;
        font-size: 50px;
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .container {
        position: relative;
    }

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: variables.$secondaryTextColor;
        content: '';
        opacity: .3;
    }

    &__text {
        font-family: 'Pacifico', cursive;
        margin-top: 64px;
        margin-bottom: 64px;
        font-size: 40px;
        line-height: 56px;
        font-weight: 800;
        text-align: center;
        color: variables.$textColor; 
    }

    &__logo {
        margin: 0 auto;
        height: 80px;
        display: block;
    }

    @media (max-width: 640px) {
        padding: 40px 0;

        &__logo {
            height: 40px;
        }

        &__text {
            font-size: 22px;
            line-height: 24px;
            margin: 24px 0;
        }
    }
}
